import SpacedUpdate from "../../../services/spaced_update.js";
import AbstractBulkAction from "../abstract_bulk_action.js";

const TPL = `
<tr>
    <td colspan="2">
        <div style="display: flex; align-items: center">
            <div style="margin-right: 10px; flex-shrink: 0;">:</div> 
            
            <input type="text" 
                class="form-control new-title" 
                placeholder="" 
                title=""/>
        </div>
    </td>
    <td class="button-column">
        <div class="dropdown help-dropdown">
            <span class="bx bx-help-circle icon-action" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false"></span>
            <div class="dropdown-menu dropdown-menu-right p-4">
                <p>Javascript,  <code>note</code> (). :</p>
                
                <ul>
                    <li><code></code>- "Note"</li>
                    <li><code>NEW: \${note.title}</code>- "NEW:"</li>
                    <li><code>\${note.dateCreatedObj.format('MM-DD:')}: \${note.title}</code>- </li>
                </ul>
                
                 API  <a href="https://zadam.github.io/trilium/backend_api/Note.html">note</a>  <a href="https://day.js.org/docs/en/display/format">dateCreatedObj / utcDateCreatedObj </a>.
            </div>
        </div>
    
        <span class="bx bx-x icon-action action-conf-del"></span>
    </td>
</tr>`;

export default class RenameNoteBulkAction extends AbstractBulkAction {
    static get actionName() { return "renameNote"; }
    static get actionTitle() { return ""; }

    doRender() {
        const $action = $(TPL);

        const $newTitle = $action.find('.new-title');
        $newTitle.val(this.actionDef.newTitle || "");

        const spacedUpdate = new SpacedUpdate(async () => {
            await this.saveAction({
                newTitle: $newTitle.val(),
            });
        }, 1000);

        $newTitle.on('input', () => spacedUpdate.scheduleUpdate());

        return $action;
    }
}
